import {FormulaService} from './../../../+builder/services/formula.service';
import {Component, Input, OnInit, ViewEncapsulation, DoCheck} from '@angular/core';
import {JSONBuilder} from '../../../+builder/services/JSONBuilder.service';
import {TemplateRendererService} from './../../services/templateRenderer.service';

declare var jQuery: any;
@Component({
  selector: 'result_output', templateUrl: './resultoutput.component.html', encapsulation: ViewEncapsulation.None
})
export class ResultOutput implements OnInit,DoCheck {
  @Input() data: any;
  @Input() devMode: any;
  formula: any;
  questions: any;
  resultNow: any;
  staticControls: any;
  formulaVal: any = 0;
  conditionalTitle: any = '';
  conditionalDesc: any = '';
  dataCheck: any = '';

  ngOnInit() {
    this.staticControls = this._templateRenderer.getStaticControls();
    /* for compute result even, it is not used in formula */
    // if (this.jsonBuilderHelper.getJSONBuilt().status === 'DEV' && this.devMode) {
    //   this.resultNow = 'Result';
    // } else {
    //   this.staticControls = this._templateRenderer.getStaticControls();
    this.resultNow = this.formulaService.formulaFunction(this.staticControls.Result.Result.items.indexOf(this.data));
    this.resultNow = '{R' + (this.staticControls.Result.Result.items.indexOf(this.data) + 1) + '}';
    // }
  }

  constructor(private jsonBuilderHelper: JSONBuilder, private _templateRenderer: TemplateRendererService, private formulaService: FormulaService) {

  }

  ngDoCheck() {
    //this.data.isIconPresent &&a
    if (this.data.config.showHelp){
      if (this.devMode) {
      if (this.dataCheck != JSON.stringify(this.data)) {
        let flag = 0;
        try {
          for (let optionIndex in JSON.parse(this.dataCheck).options) {
            if (JSON.stringify(JSON.parse(this.dataCheck).options[optionIndex]) != JSON.stringify(this.data.options[optionIndex])) {
              flag = 1;
              this.conditionalTitle = this.data.options[optionIndex].label;
              this.conditionalDesc = this.data.options[optionIndex].icon;
            }
          }
          if (flag == 0) {
            this.conditionalTitle = this.data.options[0].label;
            this.conditionalDesc = this.data.options[0].icon;
          }
        } catch (e) {}
        this.dataCheck = JSON.stringify(this.data);
      }
    } else {
      if (this.formulaVal != this.jsonBuilderHelper.getJSONBuilt().formula[this.staticControls.Result.Result.items.indexOf(this.data)].value.toString().replace(/[^0-9.]/g, "")) {
        this.formulaVal = this.jsonBuilderHelper.getJSONBuilt().formula[this.staticControls.Result.Result.items.indexOf(this.data)].value.toString().replace(/[^0-9.]/g, "");
        let flag = 0;
        for (let option of this.data.options) {
          if (Number(option.attr.class) <= this.formulaVal && this.formulaVal <= Number(option.attr.style)) {
            this.conditionalTitle = option.label;
            this.conditionalDesc = option.icon;
            flag = 1;
          }
        }
        if (flag == 0) {
          //The Default Condition
          this.conditionalTitle = this.data.config.attr.class;
          this.conditionalDesc = this.data.config.attr.style;
        }
      }
    }
  }
  }

  //ngDoCheck() {
  // if (this.jsonBuilderHelper.getJSONBuilt().status === 'DEV' ) {
  //   this.staticControls = this._templateRenderer.getStaticControls();
  //   this.resultNow = '{R' + (this.staticControls.Result.Result.items.indexOf(this.data) + 1) + '}';
  // }
  //}

}



